@include wrap-class {
	.wds-optimum-length-indicator-field {
		$text-color: #888;
		margin-bottom: 0;

		+ .wds-optimum-length-indicator {
			position: relative;
			width: 100%;
			margin-top: -2px;
			margin-bottom: $space-s;

			&,
			& span:first-child {
				height: 3px;
				display: block;
			}

			span:first-child {
				background: transparent;
				border-radius: 0 0 $radius $radius;
			}

			span:last-child {
				color: $text-color;
				display: none;
				float: right;
				font-size: 12px;
				margin-top: 8px;

				&:before {
					margin-right: $space-xxs;
				}
			}
		}

		&.over,
		&.under {
			+ .wds-optimum-length-indicator {
				span:first-child {
					background: $color-tag-yellow;
				}

				span:last-child {
					@include icon(before, warning-alert, true);

					&:before {
						color: $color-tag-yellow;
					}
				}
			}
		}

		&.almost-over,
		&.almost-under,
		&.just-right {
			+ .wds-optimum-length-indicator {
				span:first-child {
					background: $color-tag-green;
				}

				span:last-child {
					@include icon(before, check-tick, true);

					&:before {
						color: $color-tag-green;
					}
				}
			}
		}

		&.resolving {
			+ .wds-optimum-length-indicator {
				span:last-child {
					@include icon(before, loader);

					&:before {
						animation: spin 1.3s linear infinite;
						color: $text-color;
					}
				}
			}
		}
	}
}