@include wrap-class('.wds-lighthouse-settings') {
	.sui-summary {
		&:not(.sui-unbranded):not(.sui-rebranded) {
			@include media(min-width, md) {
				background-image: url("#{$image-path}graphic-lighthouse-summary.svg");
			}
		}

		.sui-summary-segment {
			overflow: visible;
		}
	}

	.sui-header {
		.sui-actions-right {
			@include media(max-width, lg) {
				margin-top: 10px;
			}
		}
	}

	.sui-sidenav {
		.sui-vertical-tab {
			.sui-tag-pro {
				top: 9px;
			}
		}

		.sui-vertical-tabs {
			.sui-tag-beta {
				position: relative;
				vertical-align: 5px;
				margin-left: 5px;
			}
		}
	}

	#wds-lighthouse-report-upsell-notice {
		.sui-notice-content {
			background: url("#{$image-path}graphic-lighthouse-modal.svg") no-repeat 40px 10px #FFFFFF;
			padding-left: 170px;

			@include media(max-width, sm) {
				background-image: none;
				padding-left: 20px;
			}
		}

		.sui-button-purple {
			margin-top: 10px;
		}
	}

	#wds-lighthouse-progress-modal {
		.wds-mascot-message {
			padding-top: 30px;
		}

		&.is-member {
			.sui-box {
				background: url("#{$image-path}graphic-lighthouse-modal.svg") no-repeat bottom center #FFFFFF;
				padding-bottom: 148px;

				@include no_branding {
					background: #FFFFFF;
					padding-bottom: 0;
				}
			}
		}
	}

	#wds-lighthouse-summary {
		.wds-lighthouse-score {
			display: flex;
			align-items: center;

			> div:not(.sui-tooltip-constrained) {
				padding-left: 10px;

				strong {
					font-size: 14px;
				}

				p {
					margin-top: 0;
				}
			}

			.sui-circle-score {
				position: relative;
				height: 80px;
				width: 80px;
				padding-left: 0;
				cursor: pointer;

				.sui-circle-score-label {
					font-size: 30px;
					position: absolute;
					margin-left: 0;
					left: 22px;
				}

				&[data-score="0"],
				&[data-score="1"],
				&[data-score="2"],
				&[data-score="3"],
				&[data-score="4"],
				&[data-score="5"],
				&[data-score="6"],
				&[data-score="7"],
				&[data-score="8"],
				&[data-score="9"] {
					.sui-circle-score-label {
						left: 32px;
					}
				}

				&[data-score="100"] {
					.sui-circle-score-label {
						left: 15px;
						font-size: 28px;
					}
				}
			}
		}

		.wds-lighthouse-tooltip {
			padding-left: 3px;

			.sui-notice-icon {
				top: -3px;
				left: 0;

				&:before {
					color: #DDDDDD;
				}
			}
		}

		.sui-summary-sub {
			margin-top: 30px;

			strong {
				font-size: 14px;
				display: block;
			}
		}
	}

	[data-remaining-minutes] {
		.sui-notice {
			margin: 0 0 30px;
		}
	}

	.wds-lh-device {
		margin-right: 10px;
		font-size: 13px;

		> a {
			color: #888888;
			padding: 12px 20px;
			background: #e6e6e6;

			&:hover {
				color: #666;
				background: #ECECEC;
			}

			span::before {
				color: #888888;
				margin-right: 5px;
			}

			&:first-child {
				border-radius: 4px 0 0 4px;
			}

			&:last-child {
				border-radius: 0 4px 4px 0;
			}

			&.active {
				background: #FFF;
				color: #17A8E3;

				span::before {
					color: #17A8E3;
				}
			}
		}
	}

	#tab_lighthouse {
		.sui-accordion {
			@include pull('left');
			@include pull('bottom');
			@include widen;

			display: flex;
			flex-direction: column;

			.sui-accordion-item {
				order: 400;

				&.sui-warning {
					order: 300;
				}

				&:last-child {
					border-bottom: 1px solid $color-border-grey;
				}
			}

			.wds-structured-data-check {
				.sui-accordion-item-header {
					.sui-button {
						align-self: flex-start;
						margin-right: 0;
						margin-left: auto;
					}

					.sui-accordion-open-indicator {
						align-self: flex-start;
						margin-right: 0;
						margin-left: 20px;
					}
				}
			}

			.sui-accordion-item-body {
				.sui-box-body {
					&,
					& p {
						color: #888888;
					}

					ul {
						list-style: outside;
						margin-left: 20px;

						li {
							padding-left: 10px;
						}
					}

					.wds-lh-toggle-container {
						.wds-lh-toggle {
							clear: both;
							display: table;

							&,
							&:hover {
								color: #17A8E3;
							}

							&:before {
								content: "";
								display: inline-block;
								width: 16px;
								height: 16px;
								background: #888888;
								margin-right: 5px;
								mask-size: 16px 16px;
								mask-position: center;
								mask-repeat: no-repeat;
								mask-image: url("#{$image-path}icons/plus-square.svg");
								vertical-align: -3px;
							}
						}

						.wds-lh-section {
							display: none;
						}

						&.open {
							.wds-lh-toggle {
								margin-bottom: 20px;

								&,
								&:hover {
									color: #888888;
								}

								&:before {
									mask-image: url("#{$image-path}icons/minus-square.svg");
								}
							}

							.wds-lh-section {
								display: block;
							}
						}
					}

					.wds-lh-section {
						margin-bottom: 25px;

						&:last-child {
							margin-bottom: 0;
						}

						strong {
							font-weight: 600;
						}

						p {
							margin-top: 10px;

							&:last-child {
								margin-bottom: 0;
							}
						}

						p strong,
						li strong {
							color: #666666;
							font-weight: 500;
						}

						strong.wds-lh-red-word {
							color: #FF6D6D;
						}

						strong.wds-lh-green-word {
							color: #1ABC9C;
						}

						.sui-notice {
							margin-top: 5px;
							margin-bottom: 0;

							p {
								margin-top: 0;
								color: #333;

								strong {
									color: #333;
								}
							}
						}

						.sui-table {
							th .sui-icon-info:before {
								color: #DDDDDD;
								margin-left: 5px;
							}
						}
					}

					.wds-lh-highlight {
						background: #F8F8F8;
						padding: 20px;
						border-radius: 4px;
						border-left: 2px solid;
						margin-bottom: 25px;
						font-family: SFMono-Regular, Consolas, "Liberation Mono", Menlo, Courier, monospace;

						&-container {
							margin-top: 25px;
							margin-bottom: 25px;
							padding: 0 13px;

							&:last-child {
								margin-bottom: 0;
							}
						}

						&-success {
							border-color: #1ABC9C;
						}

						&-error {
							border-color: #FF6D6D;
						}

						&:last-child {
							margin-bottom: 0;
						}

						.wds-lh-tag {
							color: #0153CC;
						}

						.wds-lh-attr {
							color: #17A8E3;
						}
					}

					.wds-lh-font-sizes-table {
						th, td {
							&:not(:first-child) {
								width: 20%;
							}
						}
					}
				}
			}
		}

		.wds-lighthouse-screenshot {
			max-height: var(--element-screenshot-scaled-height);
			max-width: var(--element-screenshot-scaled-width);
			overflow: hidden;
			border: 2px solid #ddd;

			&-container {
				display: none;
			}

			&-inner {
				position: relative;
				transform: scale(var(--element-screenshot-scale));
				transform-origin: top left;
				width: var(--element-screenshot-width);
				height: var(--element-screenshot-height);
			}

			&-frame {
				position: relative;
				top: var(--element-screenshot-top-offset);
			}

			&-image {
				background: var(--element-screenshot-url) no-repeat center center;
				width: var(--element-screenshot-width);
				height: var(--element-screenshot-height);
			}

			&-marker {
				position: absolute;
				outline: 2px solid #d3e156;
				border: 2px solid #d3e156;
				z-index: 1;
				height: var(--element-screenshot-highlight-height);
				width: var(--element-screenshot-highlight-width);
				left: var(--element-screenshot-highlight-left);
				top: var(--element-screenshot-highlight-top);
			}

			&-clip {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: rgba(0, 0, 0, 0.5);
				clip-path: polygon(
						0% 0%,
						0% 100%,
						var(--element-screenshot-highlight-left) 100%,
						var(--element-screenshot-highlight-left) var(--element-screenshot-highlight-top),
						var(--element-screenshot-highlight-left-width) var(--element-screenshot-highlight-top),
						var(--element-screenshot-highlight-left-width) var(--element-screenshot-highlight-top-height),
						var(--element-screenshot-highlight-left) var(--element-screenshot-highlight-top-height),
						var(--element-screenshot-highlight-left) 100%,
						100% 100%,
						100% 0%
				);
			}
		}

		.sui-table {
			.wds-lighthouse-screenshot {
				border-radius: 3%;
				cursor: pointer;

				&:hover {
					border: 2px solid #FECF2F;
				}
			}
		}

		#wds-lighthouse-screenshot-zoom {
			.sui-box {
				background: transparent;

				.sui-box-header {
					padding: 0;
					border-bottom: none;
				}

				.sui-box-body {
					padding: 10px 0 0 0;
				}
			}
		}

		#tap-targets.sui-accordion-item {
			.sui-table {
				th:nth-child(2) {
					width: 100px;
				}
			}
		}

		.wds-lh-screenshot-th {
			width: 240px;
		}
	}

	#tab_reporting {
		.sui-disabled {
			@include pull('left');
			@include widen;
			@include margin('top');
			@include gutter('top');
			@include gutter('right');
			@include gutter('bottom');
			@include gutter('left');
		}

		.wds-mascot-message {
			@include pull('bottom');

			.sui-upsell-image {
				margin-bottom: 0;
			}
		}

		.wds-recipient-summary-notice {
			margin: 20px 0 30px;
		}

		#wds-lighthouse-reporting-device {
			[data-panes] {
				display: none;
			}
		}

		#wds-reporting-condition-container {
			width: 125px;
			padding-left: 25px;
			padding-top: 20px;
		}

		#wds-lighthouse-reporting-device-container {
			@include pull('left');
			@include gutter('left');
			@include gutter('right');
			@include gutter('top');
			@include widen;
			border-top: 1px solid $color-border-grey;
			padding-top: 30px;

			p.sui-description {
				margin-bottom: 10px;
			}
		}
	}
}
