// Dashboard
@include wrap-class('.wds-dashboard') {

	.sui-list-detail,
	small + .wds-right {
		small {
			font-weight: 500;
		}
	}

	.wds-onboarding-dialog {
		.wds-progress {
			margin-bottom: 0;
		}

		.wds-toggle {
			order: 2;

			label {
				margin-right: 0;
				margin-left: $space-xs;
			}
		}

		.sui-box {
			background: url("#{$image-path}graphic-top-large.svg") no-repeat bottom center #FFFFFF;
			padding-bottom: 130px;
			background-size: 130px;

			@include no_branding {
				background: #FFFFFF;
				padding-bottom: 0;
			}

			.sui-notice {
				margin-top: $space;
			}

			.wds-separator-top {
				@include pull('left');
				@include gutter('left');
				@include gutter('right');
				@include widen;
				margin-top: $space;
				padding-top: $space;
			}

			.sui-box-footer {
				justify-content: space-between;

				p {
					max-width: 430px;
				}

				.wds-onboard-footer-desc {
					flex-shrink: 2;

					p {
						width: 70%;
					}
				}

				.wds-onboard-footer-skip {
					flex-basis: 50px;
				}

				.wds-onboard-footer-start {
					flex-basis: 120px;
				}
			}

			.onboard-skip {
				display: inline-block;
				color: #888888;
				margin-right: 20px;
				font-size: 13px;

				&:hover {
					color: darken(#888888, 10%);
					text-decoration: underline;
				}
			}
		}

		&.wds-onboarding-in-progress {
			[data-modal-close] {
				display: inline-block;
				margin-right: 0;
			}
		}
	}

	#wds-welcome-modal {
		.sui-box-body {
			padding-top: $space-s;

			p.sui-description {
				span:first-child {
					margin-bottom: 10px;
					display: block;
				}

				span:last-child {
					margin-top: 8px;
					display: block;
				}
			}
		}

		h4, ul {
			text-align: left;
		}

		h4 {
			color: black;
			font-size: $font-size-s;
			line-height: 1;
			font-weight: 500;
		}

		ul {
			margin-bottom: $space;

			li {

				&:before {
					content: "";
					display: inline-block;
					width: 5px;
					height: 5px;
					border-radius: 5px;
					background: $color-dark-alt;
					vertical-align: middle;
					margin-right: $space-xs;
				}

				> h4 {
					display: inline-block;
				}
			}
		}

		&-skip {
			color: #ffffff;
			font-weight: 200;
			font-size: $font-size-s;
			margin: 0 auto;
			display: table;
		}

		.sui-button-float--right {
			&:hover, &:focus {
				background-color: rgba(0, 0, 0, 0.2);
			}

			span:before {
				color: #333333;
			}

			@include no_branding {
				span:before {
					color: #888888;
					font-size: 16px;
				}
			}
		}

		@include no_branding {
			.sui-box-banner {
				display: none;
			}

			.sui-box-header {
				padding-top: 60px;
			}
		}

		&-dialog-description {
			strong {
				color: #666666;
			}
		}

		.sui-box-body {
			padding-left: 0;
			padding-right: 0;
		}
	}

	.sui-box.wds-dashboard-widget {
		.sui-box-body {
			p {
				margin-top: 0;
				margin-bottom: $space-xs;

				&:last-child {
					margin-bottom: 0;
				}
			}

			> p:first-child {
				margin-top: 0;
			}

			.sui-notice {
				margin: $space-m 0 $space 0;

				&:first-child {
					margin-top: 0;
				}

				&:last-child {
					margin-bottom: 0;
				}

				p {
					margin: 0;
				}
			}

			.sui-progress-block {
				margin-top: $space-m;
			}
		}
	}

	#wds-dashboard-stats {
		.sui-summary-segment {
			overflow: visible;
		}

		#wds-lighthouse-summary {
			.wds-lighthouse-score {
				display: flex;
				align-items: center;

				> div:not(.sui-tooltip-constrained) {
					padding-left: 10px;

					strong {
						font-size: 14px;
					}

					p {
						margin-top: 0;
					}
				}

				.sui-circle-score {
					position: relative;
					height: 80px;
					width: 80px;
					padding-left: 0;
					cursor: pointer;

					.sui-circle-score-label {
						font-size: 30px;
						position: absolute;
						margin-left: 0;
						left: 22px;
					}

					&[data-score="0"],
					&[data-score="1"],
					&[data-score="2"],
					&[data-score="3"],
					&[data-score="4"],
					&[data-score="5"],
					&[data-score="6"],
					&[data-score="7"],
					&[data-score="8"],
					&[data-score="9"] {
						.sui-circle-score-label {
							left: 32px;
						}
					}

					&[data-score="100"] {
						.sui-circle-score-label {
							left: 15px;
							font-size: 28px;
						}
					}
				}
			}

			.wds-lighthouse-tooltip {
				padding-left: 3px;

				.sui-notice-icon {
					top: -3px;
					left: 0;

					&:before {
						color: #DDDDDD;
					}
				}
			}
		}

		.wds-summary-message {
			> p {
				margin-top: 3px;
				margin-bottom: 5px;
			}
		}

		&.sui-summary {
			.sui-summary-details .sui-icon-loader {
				margin-right: 40px;
				top: 0;
			}
		}
	}

	#wds-reports-box,
	#wds-lighthouse {
		.wds-lighthouse-icon {
			background: url("#{$image-path}graphic-icon-lighthouse.svg") no-repeat;
			width: 16px;
			height: 16px;
			display: inline-block;
			vertical-align: -1px;
			margin-right: 10px;
		}
	}

	#wds-reports-box {
		.wds-lighthouse-icon {
			margin: 0 3px;
		}

		.sui-icon-smart-crawl,
		.sui-icon-web-globe-world {
			&:before {
				color: #000;
			}
		}
	}

	#wds-lighthouse {
		.sui-accordion {
			@include pull('left');
			@include widen;
			@include margin('top');
			border-bottom: none;
		}

		.wds-view-report {
			@include pull('left');
			@include widen;
			@include gutter('top');
			@include gutter('left');
			@include gutter('right');
			border-top: 1px solid $color-border-grey;
		}

		#wds-device-tag {
			font-size: 9px;
			padding: 2px 6px;
			line-height: 1;
			min-height: 0;
		}
	}

	#wds-dashboard-stats,
	#wds-lighthouse {
		.wds-run-test-onload {
			@extend .sui-button-onload;
		}
	}

	#wds-advanced-tools-dashboard-box {
		.sui-upsell-notice {
			@media (min-width: map-get($sui-breakpoints, 'lg')) {
				margin-top: 20px;
			}
		}
	}

	#wds-content-analysis-box {
		.sui-box {
			border: 1px solid $color-box-header-border;

			&-body {
				> p {
					margin-bottom: $space-m;
				}
			}
		}

		.sui-accordion-item-body > p {
			margin-top: 0;
		}

		.wds-report {
			section.sui-accordion {
				margin-top: 0;
				margin-bottom: 0;

				.sui-accordion-item-header {
					transition: none;
				}

				.sui-accordion-item:not(.sui-accordion-item--open) {
					.sui-accordion-item-header {
						box-shadow: none;
					}
				}

				.sui-tag {
					align-self: flex-end;
					margin-right: 0;
					margin-left: auto;
				}

				&:last-of-type {
					border-top-width: 0;
				}

				&:first-of-type {
					border-top-width: 1px;
				}

				&:last-child {
					margin-bottom: -$space;
				}

				&:only-of-type {
					&:first-child {
						margin-bottom: $space;
					}

					&:last-child {
						margin-top: $space;
					}
				}
			}
		}

		.sui-table {
			border: none;
		}
	}

	#wds-sitemap-box {
		.sui-notice-warning {
			margin: $space-xs 0 $space-xs 0;
		}

		div:nth-of-type(2) {
			> small {
				line-height: 27px;
			}
		}

		.wds-sitemap-type-tag {
			font-weight: 600;
			line-height: 16px;
			min-height: 20px;
			font-size: 10px;
			padding: 0 9px;
			cursor: pointer;
		}
	}

	#wds-reports-box {
		.wds-mascot-message {
			margin-top: $space-xs;

			img {
				width: 85px;
			}
		}

		.sui-box-body {
			[class^="sui-icon-"] {
				margin-right: $space-xxs;
				font-size: $font-size;
			}

			.sui-table {
				border-radius: 0;
				border-left: none;
				border-right: none;

				td:not(:first-child) {
					text-align: right;
				}

				td:last-child {
					width: 80px;
				}
			}

			.sui-description {
				text-align: center;
			}
		}
	}

	#wds-upgrade {
		ul {
			margin-top: $space;
			margin-bottom: $space;

			li {
				@include icon(before, check, true);

				&:before {
					vertical-align: middle;
					color: $color-purple;
					margin-right: $space-xs;
				}

				color: $color-dark-alt;
				margin-bottom: $space-m;
				font-weight: 500;
			}
		}
	}

	#wds-schema-box {
		.wds-separator-bottom {
			margin-bottom: 30px;
		}

		.wds-default-schema {
			.sui-tooltip {
				margin-left: 10px;
			}

			.sui-icon-info {
				&:before {
					color: #DDDDDD;
				}

				&:hover:before {
					color: #888888;
				}
			}
		}
	}
}
