@include wrap-class {
	#wds-crawl-summary-container {
		.sui-summary {
			&:not(.sui-unbranded):not(.sui-rebranded) {
				@include media(min-width, md) {
					background-image: url($summary-image);
					background-size: 171px 161px;
				}
			}

			.sui-summary-details {
				.sui-icon-loader {
					top: 0;
					left: 0;
					margin-bottom: 10px;
					font-size: 18px;
				}
			}
		}
	}

	.wds-no-crawl-issues {
		pointer-events: none;
	}

	.wds-crawl-issues-table {
		td:first-child {
			box-shadow: inset 2px 0 0 0 #888888;

			.sui-icon-warning-alert {
				margin-right: 8px;
			}
		}

		tr.wds-controls-row td,
		tr.wds-no-results-row td {
			box-shadow: none;
		}
	}

	.sui-warning {
		.wds-crawl-issues-table {
			td:first-child {
				box-shadow: inset 2px 0 0 0 $color-tag-yellow;
			}

			tr.wds-controls-row td,
			tr.wds-no-results-row td {
				box-shadow: none;
			}
		}
	}

	.wds-crawl-issues-table,
	.wds-ignored-items-table {
		@include pull('left');
		@include widen;
		border-top: 1px solid $color-border-grey;
		border-collapse: collapse;

		td {
			border-bottom: 1px solid $color-border-grey;
			padding-top: $space-m - 3px;
			padding-bottom: $space-m - 3px;
		}

		tr:last-child {
			td {
				border: none;
				padding-bottom: 0;
			}
		}

		tr.wds-controls-row {
			td {
				padding-top: $space;
			}
		}

		.wds-controls-row-flex {
			display: flex;
			justify-content: space-between;
		}

		td:first-child {
			@include gutter('left');
		}

		td:last-child {
			@include gutter('right');

			.sui-dropdown {
				margin-right: 0;
			}
		}

		td:not(:first-child) {
			text-align: right;
		}
	}

	.wds-report {
		.sui-tabs {
			[data-tabs] {
				@include pull('left');
				@include widen;
				@include gutter('left');
			}

			[data-panes] > * {
				padding: 0;
				border-top: none;
			}
		}

		.sui-modal-content {
			.sui-box {
				background: url("#{$image-path}graphic-occurrences-modal.svg") no-repeat bottom -20px center #FFFFFF;
				padding-bottom: 150px;
				background-size: 180px;

				@include no_branding {
					background: #FFFFFF;
					padding-bottom: 0;
				}

				.sui-form-field {
					.sui-description {
						text-align: left;
						margin-top: $space-xs;
					}
				}

				ul {
					@include small-text;
				}
			}
		}

		.wds-url-crawler-progress {
			$bg-size: 180px;

			background: url("#{$image-path}graphic-occurrences-modal.svg") no-repeat bottom -#{$space-m} center #FFFFFF;
			background-size: $bg-size;
			padding-bottom: $bg-size + $space-xs;

			@include no_branding {
				background: #FFFFFF;
				padding-bottom: $space;
			}

			@include pull('bottom');
		}

		#wds-issue-redirect {
			.sui-box-body {
				button:first-of-type {
					float: left;
				}

				button:last-of-type {
					float: right;
				}

				@include cf;
			}
		}

		#wds-issue-occurrences {
			.sui-box-body button {
				margin-top: $space-m;
			}
		}
	}
}
