@include wrap-class {

	.wds-optimum-indicator {
		position: relative;
		width: 100%;
		margin-top: -2px;
		margin-bottom: $space-s;

		&,
		span:first-child {
			height: 3px;
			display: block;
		}

		span:first-child {
			background: transparent;
			border-radius: 0 0 $radius $radius;
		}

		span:last-child {
			float: right;
			font-size: 12px;
			margin-top: 8px;

			&:before {
				margin-right: $space-xxs;
			}
		}

		&.over,
		&.under {

			span:first-child {
				background: $color-tag-yellow;
			}

			span:last-child {
				@include icon(before, warning-alert, true);

				&:before {
					color: $color-tag-yellow;
				}
			}
		}

		&.almost-over,
		&.almost-under,
		&.just-right {

			span:first-child {
				background: $color-tag-green;
			}

			span:last-child {
				@include icon(before, check-tick, true);

				&:before {
					color: $color-tag-green;
				}
			}
		}

		&.resolving {

			span:last-child {
				@include icon(before, loader);

				&:before {
					animation: spin 1.3s linear infinite;
				}
			}
		}
	}
}