@mixin small-text() {
	font-size: $font-size-s;
	line-height: 22px;
	color: #888888;
	font-weight: 400;
}

@mixin wrap-class($wrap: '') {
	#{$sui-class} .sui-wrap.wds-page.wrap.wrap-wds#{$wrap} {
		@content;
	}
}

@mixin pull($position: '') {
	@if $position != '' {
		margin-#{$position}: -$space-gutter;

		@include media(max-width, md) {
			margin-#{$position}: -$space-gutter-md;
		}
	}
}

@mixin widen {
	width: calc(100% + #{$space-gutter * 2});

	@include media(max-width, md) {
		width: calc(100% + #{$space-gutter-md * 2});
	}
}

@mixin gutter($position: '') {
	@if $position == '' {
		padding: $space-gutter;

		@include media(max-width, md) {
			padding: $space-gutter-md;
		}
	} @else {
		padding-#{$position}: $space-gutter;

		@include media(max-width, md) {
			padding-#{$position}: $space-gutter-md;
		}
	}
}

@mixin margin($position: '') {
	@if $position == '' {
		margin: $space-gutter;

		@include media(max-width, md) {
			margin: $space-gutter-md;
		}
	} @else {
		margin-#{$position}: $space-gutter;

		@include media(max-width, md) {
			margin-#{$position}: $space-gutter-md;
		}
	}
}

@mixin is_member {
	$full-selector: '' + &;
	$is-member: str-replace($full-selector, $sui-class, $sui-class + '.wds-is-member');

	@at-root #{$is-member} {
		@content;
	}
}

@mixin no_branding {
	$full-selector: '' + &;
	$no-branding: str-replace($full-selector, $sui-class, $sui-class + '.wds-no-branding');

	@at-root #{$no-branding} {
		@content;
	}
}

@mixin high_contrast {
	$full-selector: '' + &;
	$high-contrast: str-replace($full-selector, $sui-wrap-class, $sui-wrap-class + '.sui-color-accessible');

	@at-root #{$high-contrast} {
		@content;
	}
}

@mixin cf {
	&:after {
		content: "";
		display: table;
		clear: both;
	}
}
