@include wrap-class {
	.og-images {
		$box-size: 40px;
		$box-border-radius: 3px;

		@include cf;
		width: 100%;

		.add-action-wrapper,
		.item {
			float: left;
			text-align: center;
			margin-right: 10px;
			margin-top: 5px;
			margin-bottom: 5px;

			a {
				display: block;
				line-height: $box-size;
				border-radius: $box-border-radius;
			}
		}

		.add-action-wrapper {
			a {
				background-color: #FAFAFA;
				box-shadow: none;
				width: $box-size;
				height: $box-size;

				border: 1px solid #DDDDDD;
				@include high_contrast {
					border: 1px solid $color-black;
				}

				&:hover {
					background: #ffffff;

					span:before {
						color: #333;
					}
				}
			}
		}

		.item {
			position: relative;
			width: $box-size;
			height: $box-size;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: $box-border-radius;
				box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
			}

			.remove-action {
				position: absolute;
				top: 0;
				left: 0;
				width: $box-size;
				height: $box-size;
				opacity: 0;
				background: rgba(0, 0, 0, 0.8);

				&:hover {
					opacity: 1;
				}

				span:before {
					color: #FFF;
				}
			}
		}
	}
}
