@include wrap-class('.wds-page-autolinks') {

	.sui-box-settings-row {

		&.sui-flushed {
			background: #f8f8f8;
			padding-top: 20px;
			border-bottom: 0;

			@media (min-width: 783px) {
				padding-top: 30px;
			}
		}
	}

	.sui-field-list {
		background: #ffffff;
	}

	.sui-modal-content {
		.sui-label {
			justify-content: space-between;
		}
	}

	.wds-keyword-pairs {
		margin-top: 10px;
		margin-bottom: 10px;

		tbody {

			td:last-child {
				text-align: right;

				.sui-modal {
					.sui-box {
						text-align: left;
					}
				}
			}
		}
	}

	[href="#remove"] {
		color: $color-red;

		span.sui-icon-trash:before {
			color: $color-red;
		}
	}

	.wds-postlist-list {
		table.wds-postlist,
		.wds-notice {
			margin-top: $space-xs;
			margin-bottom: $space-xs;
		}

		table.wds-postlist {
			&.wds-postlist-empty_list {
				display: none;
			}
		}

		.wds-postlist-item-remove {
			text-align: right;
		}

		.wds-notice {
			display: none;

			&.wds-postlist-empty_list {
				display: block;
			}
		}
	}

	.wds-breadcrumb-previews {
		font-size: $table--text-font-size;
		line-height: $table--text-line-height;
	}

	.wds-breadcrumb-preview {
		display: flex;
		padding: 5px 0px;

		&-label {
			width: 100%;
			max-width: 70px;
		}

		&-snippets {

			strong {
				color: $info;
				text-decoration: underline;
			}
			.prefix{
				margin-right:10px;
			}

			.sui-icon {
				font-size: 13px;
				margin: 0 $space-xxs;
			}
		}

		&-expander {

			&-text {
				text-decoration: underline;
			}

			.sui-icon {
				margin-left: $space-xxs;

				&:before {
					color: $info;
					font-size: $form--label-font-size;
				}
			}
		}
	}

	.wds-custom-separator {
		margin-top: $space-xs;
	}

	#tab_automatic_linking {

		#tab-content--post_types {
			.sui-box-settings-header {
				margin-bottom: 0 !important;
			}
		}
	}

	.tab_url_redirection_main {
		@include margin('bottom');

		.sui-modal {
			.sui-box-body {
				.sui-form-field {
					.sui-description {
						text-align: left;
					}
				}

				[data-modal-close] {
					float: left;
				}

				.wds-action-button {
					float: right;
				}

				@include cf;
			}

			.sui-accordion {
				margin-bottom: 30px;
				@include pull('left');
				@include widen;

				.sui-accordion-item-body {
					text-align: left;

					.sui-side-tabs {
						margin: 15px 0 15px;
					}
				}
			}
		}

		#wds-redirects-container {
			.wds-no-redirects-message {
				display: none;
			}

			.wds-no-redirects {
				background-image: url("#{$image-path}graphic-occurrences-modal.svg");
				background-position: bottom -20px center;
				background-repeat: no-repeat;
				padding-bottom: 180px;
				background-size: 180px;

				@include no_branding {
					background: $color-bg-grey;
					padding-bottom: $space-m;
				}

				.wds-no-redirects-message {
					text-align: center;
					display: block;
				}

				.wds-redirect-controls {
					display: none;
				}
			}

			.wds-redirect-controls {
				display: flex;
				align-items: center;
				justify-content: space-between;
				@include margin('bottom');
			}
		}

		.sui-box-builder {
			border-radius: 0;
			border-width: 0;

			@include pull('left');
			@include pull('bottom');
			@include widen;
			@include margin('top');

			.sui-box-builder-header {
				padding-top: 0;
				@include gutter('bottom');
			}

			.wds-redirect-item {
				$parent: &;
				display: flex;
				align-items: center;
				padding-left: 20px;
				padding-right: 20px;

				small {
					margin-bottom: 0;
				}

				&.wds-redirect-item-columns {
					small {
						margin-bottom: 5px;
					}
				}

				&.wds-redirect-has-title {
					.wds-redirect-item-checkbox,
					.wds-redirect-item-source,
					.wds-redirect-item-destination, {
						align-self: baseline;
					}
				}

				&-columns {
					align-items: baseline;
				}

				&-checkbox {
					flex-basis: 25px;
					width: 25px;
					min-width: 25px;
				}

				&-source {
					flex-basis: 30%;
					width: 30%;
					min-width: 30%;

					display: flex;
					flex-direction: column;
					align-items: flex-start;
				}

				.sui-tooltip {
					display: block;
					max-width: 90%;
				}

				&-source-trimmed {
					color: #333;
					font: 500 13px/22px "Roboto", Arial, sans-serif;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
					max-width: fit-content;
				}

				&-destination {
					flex-basis: 40%;
					width: 40%;
					min-width: 40%;

					@include media(min-width, md) {
						flex-basis: 45%;
						width: 45%;
						min-width: 45%;
					}

					small {
						display: block;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						max-width: 90%;
					}
				}

				&-options {
					flex-basis: 200px;
				}

				&-dropdown {
					flex-basis: 40px;
				}

				.sui-tag {
					margin-top: 5px;
					margin-bottom: 5px;
				}
			}
		}

		#wds-import-redirects-modal {
			.sui-form-field,
			.sui-label {
				margin-bottom: 10px;
			}

			.sui-notice-error {
				margin: 10px 0;
			}
		}
	}

	.tab_moz {
		.wds-disabled-component {
			padding-bottom: 0;
		}

		.wds-moz-fields {
			background: $color-bg-grey;

			@include pull('left');
			@include widen;
			@include gutter;

			p {
				text-align: center;
			}

			&-inner {
				@include cf;
				max-width: 300px;
				margin: 0 auto;

				input[type="submit"] {
					margin-right: 0;
					float: right;
				}
			}
		}

		.wds-moz-signup-notice {
			text-align: center;
		}

		.sui-wrap {
			margin-right: 0;
			margin-left: 0;
		}

		.sui-table {
			td {
				padding-top: $space-s;
				padding-bottom: $space-s;
			}
		}
	}

	.tab_robots_editor {
		.wds-disabled-component {
			.wds-notice {
				text-align: left;

				&, p {
					margin-bottom: 0;
				}
			}
		}

		textarea[readonly] {
			color: $color-dark-alt;
			font-family: monospace;
			border: 0;
			font-weight: 500;
			padding: $space-m;

			&:hover,
			&:focus {
				background: $color-input-bg;
			}
		}

		#robots-preview,
		#robots-file-contents {
			height: 120px;
			min-height: 120px;
			overflow: hidden;
			transition: none;
			resize: none;
		}

		#robots-file-contents {
			min-height: 220px;
		}

		div.sui-box-settings-row:last-of-type {
			border-bottom: none;
		}

		.sui-box-footer {
			@include pull('top');
			@include pull('right');
			@include pull('bottom');
			@include pull('left');
		}

		.wds-auto-sitemap-addition {
			@include margin('top');
		}
	}

	#tab_woo {
		.sui-box-body > p {
			@include gutter('bottom');
			@include margin('bottom');
			border-bottom: 1px solid $color-border-grey;
		}

		.sui-notice.sui-notice-info {
			margin-left: 44px;
			margin-top: -20px;
		}

		.sui-box-footer {
			display: flex;
			justify-content: space-between;
			width: 100%;
		}
	}
}
