@include wrap-class('.wds-sitemap-settings') {
	.sui-icon-defer:before {
		transform: rotate(90deg);
	}

	.sui-actions-right {
		> span {
			@include small-text;
			margin-right: $space-s;
		}
	}

	.sui-list-label {
		.sui-tooltip {
			margin-left: 3px;
			vertical-align: top;
		}
	}

	.wds-sitemap-parts {
		tbody {
			td:last-child:not(:first-child) {
				display: flex;
				justify-content: flex-end;
			}
		}

		.wds-nested-sitemap-parts {
			height: 0;
			min-height: 0;
			padding-top: 0;

			.sui-table {
				@include margin('bottom');
			}
		}

		.wds-sitemap-buddypress {
			&-groups,
			&-profiles {
				td {
					border-bottom: none;
				}
			}
		}
	}

	.tab_url_crawler_reporting {
		.sui-box-body {
			overflow: hidden;
		}

		.wds-mascot-message {
			min-height: 100px;

			.sui-upsell-image {
				margin-bottom: -$space - 40px;
			}
		}

		.sui-tabs {
			.wds-dow {
				// Hidden by default and displayed using JS
				display: none;
			}
		}

		.wds-upsell-tab-description {
			.wds-notice {
				@include margin('top');
			}
		}

		#wds-crawler-frequency-tabs {
			margin-bottom: $space-xs;
		}

		#wds-crawler-frequency-pane {
			display: block;
			max-width: 570px;
		}
	}

	.tab_settings {
		#wds-sitemap-type-tabs {
			.sui-row {
				margin-top: 30px;
				margin-bottom: 10px;
			}
		}

		.sui-description {
			.sui-notice {
				margin-top: $space-s;
			}
		}

		#wds-automatic-sitemap-updates-tabs,
		#wds-auto-notify-engines-tabs {
			.sui-tab-boxed:first-child {
				display: none;
			}
		}

		#wds-troubleshoot-sitemap-modal {
			.wds-troubleshoot-header {
				position: absolute;
				width: 100%;
				top: 0;
				left: 0;
				right: 0;
				border-radius: 5px 5px 0 0;
				background: #F8F8F8;
				padding-top: 30px;

				> span {
					background: #FFFFFF;
					margin-bottom: -49px;
					border-radius: 100%;
					height: 100px;
					width: 100px;
					display: block;
					margin-left: auto;
					margin-right: auto;
					box-shadow: 0 2px 4px rgba(243, 243, 243, 50%);
				}

				.sui-icon-check-tick,
				.sui-icon-cross-close {
					font-size: 50px;
					padding: 25px;
				}

				span.wds-troubleshoot-wrench-and-screw-icon {
					background: #FFFFFF url("#{$image-path}graphic-troubleshoot-icon.svg") no-repeat center;
				}
			}

			h3 {
				margin-top: 110px;
			}

			.sui-progress-state {
				margin-bottom: 30px;
			}

			.sui-accordion {
				@include pull('left');
				@include widen;
				border-bottom: 0;

				.sui-accordion-item-body {
					text-align: left;
				}
			}

			.wds-troubleshoot-check-again-footer {
				@include pull('left');
				@include widen;
				padding: $space $space 0 $space;
				display: flex;
				justify-content: space-between;
				border-top: 1px solid $color-border-grey;
			}

			.wds-troubleshoot-open-sitemap-button {
				margin-bottom: $space;
			}

			.sui-box-body {
				padding-top: 15px;

				.sui-progress-block {
					margin-top: 15px;
				}
			}
		}
	}

	.tab_news {
		.sui-builder-field {
			&.disabled {
				background: transparent;
				box-shadow: none;
				border: 1px solid #ddd;

				span {
					color: #AAAAAA;
				}
			}

			.sui-builder-field-label {
				span:last-child {
					font-weight: 400;
					margin-left: 5px;
					color: #AAAAAA;
				}
			}

			.sui-box {
				box-shadow: none;

				.sui-box-body {
					padding: 0;

					.wds-news-taxonomy {
						padding-bottom: 15px;
						margin-bottom: 30px;
						border-bottom: 1px solid #E6E6E6;

						&:last-child {
							margin-bottom: 0;
						}

						.wds-news-term {
							display: inline-block;
							width: 50%;
							color: #666666;
							font-weight: 500;
							margin: 5px 0;
							vertical-align: top;

							.sui-checkbox {
								margin-right: 8px;
							}
						}
					}
				}
			}

			.sui-accordion-item-header {
				.sui-checkbox {
					display: inline-flex;
					margin: 0 15px 0 0;
				}
			}
		}
	}
}
