@mixin schema_icon($icon) {
	.wds-custom-icon-#{$icon} {
		mask-image: url("#{$image-path}icons/#{$icon}.svg");
	}
}

@include wrap-class {
	[class*="wds-custom-icon-"] {
		display: inline-block;
		width: 16px;
		height: 16px;
		background: #888888;
		margin-right: 10px;
		mask-size: 16px 16px;
		mask-position: center;
		mask-repeat: no-repeat;
	}

	.sui-box-selector input + span [class*="wds-custom-icon-"] {
		width: 30px;
		height: 20px;
		margin-right: 5px;
		background: #AAAAAA;

		@include high_contrast {
			background: #000000 !important;
		}
	}

	.sui-box-selector input:checked + span [class*="wds-custom-icon-"] {
		background: #17A8E3;
	}

