@include wrap-class {
	.wds-box-blocked-area {
		padding-bottom: $space-m;
		padding-top: $space-m;
		padding-left: $space;
		padding-right: $space;
		position: relative;
		z-index: 1;

		&::before {
			background: rgba(242, 242, 242, 0.5);
			content: "";
			height: 100%;
			left: 0;
			position: absolute;
			top: 0;
			width: 100%;
			display: block;
			z-index: 2;
		}
	}
}
