.postbox-container {
	// Stuff specific to the old metaboxes goes in here
	#wds-wds-meta-box {
	}
}

.block-editor-page {
	// Stuff specific to the Gutenberg metaboxes goes in here
	#wds-wds-meta-box {
		.sui-accordion-item {
			border-right: 1px solid $color-border-grey;
		}

		#wds-readability-stats {
			border-radius: $radius;
		}
	}
}

#wds-wds-meta-box {
	// Stuff outside the main content of the metabox goes here.

	> h2 {
		span {
			position: relative;

			&::before,
			&::after {
				border-radius: 100%;
				content: "";
				display: inline-block;
				height: 10px;
				position: absolute;
				top: 3px;
				width: 10px;
			}

			&::before {
				left: calc(100% + 6px);
			}

			&::after {
				left: calc(100% + 25px);
			}
		}
	}

	&.wds-seo-success {
		> h2 span::before {
			background: $color-tag-green;
		}
	}

	&.wds-seo-warning {
		> h2 span::before {
			background: $color-tag-yellow;
		}
	}

	&.wds-seo-invalid {
		> h2 span::before {
			background: $color-border-grey;
		}
	}

	&.wds-readability-invalid {
		> h2 span::after {
			background: $color-border-grey;
		}
	}

	&.wds-readability-error {
		> h2 span::after {
			background: $color-tag-red;
		}
	}

	&.wds-readability-warning {
		> h2 span::after {
			background: $color-tag-yellow;
		}
	}

	&.wds-readability-success {
		> h2 span::after {
			background: $color-tag-green;
		}
	}

	.sui-tag {
		&.sui-tag-green {
			min-height: 12px;
			padding: 2px 8px 1px;
			border: 0;
			border-radius: 6px;
			font-size: 8px;
			line-height: 9px;
		}
	}
}

#wds_seomoz_urlmetrics,
#wds-wds-meta-box {
	.inside {
		margin: 0;
		padding: 0;
		overflow-x: unset !important;
	}
}

@include wrap-class('.wds-metabox') {
	&.wrap {
		margin: 0;
		padding: 0;
	}

	.sui-tabs {

		.sui-tab-content {

			.wds-metabox-section:first-child {
				padding-top: 0;
			}
		}
	}

	#wds-metabox-container {

		> .sui-tabs {

			> .sui-tabs-menu {
				border-top: none;
				padding-left: $space;
			}
		}
	}

	.sui-tab-item {

		.wds-issues {
			display: inline-block;
			margin-left: $space-xxs;

			&.wds-item-loading {
				@include icon(before, loader);

				&:before {
					animation: spin 1.3s linear infinite;
				}

				span {
					display: none;
				}
			}

			&-success {
				@include icon(before, check-tick);

				&:before {
					color: $color-tag-green;
				}
			}

			&-invalid,
			&-warning,
			&-error {
				height: 18px;
				width: 26px;
				border-radius: 9px;
				font-size: 10px;
				padding: 4px 0;
				text-align: center;
				line-height: 1;
				margin-left: $space-xxs + 2px;
				color: $color-dark-alt;
				font-weight: bold;
				background: $color-bg-grey;
				vertical-align: 2px;
			}

			&-invalid {
				background: $color-border-grey;
			}

			&-warning {
				background: $color-tag-yellow;
			}

			&-error {
				color: #fff;
				background: $color-tag-red;
			}
		}
	}

	[data-panes] {
		> div {
			padding-top: 0;
		}
	}

	.wds-metabox-section {
		@include gutter;
		border-bottom: 1px solid $color-border-grey;

		&:last-child {
			border-bottom: none;
		}

		> p {
			color: #888;
		}
	}

	@at-root .wds-ignored-item-header {
		@extend .sui-accordion-item-header;
	}

	.sui-accordion-item.disabled {
		.wds-ignored-item-header {
			border-radius: 0;
			cursor: default;

			&:hover {
				background-color: inherit;
			}

			button {
				cursor: pointer;
				margin-left: auto;

				[class*="sui-icon-"] {
					margin-right: 0;
				}
			}
		}
	}

	.wds-accordion {
		@include pull('left');
		@include widen;
	}

	.wds-ignore-container {
		@include gutter('top');
	}

	.wds-metabox-preview {
		.wds-preview-container {
			padding-bottom: $space-xs;
		}
	}
	// SEO tab
	.wds_seo {
		.sui-label span {
			font-weight: 400;
			margin-left: 3px;
		}

		label[for='wds_focus'] .sui-icon-info:before {
			color: #333;
		}

		.wds-edit-meta {

			.sui-border-frame {
				> .sui-form-field:nth-child(2) {
					margin-top: 40px;
				}

				> .sui-description {
					text-align: center;
				}
			}
		}

		.wds-seo-analysis-label {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: $space-m;
		}

		.wds-mascot-message {
			overflow: hidden;
			margin-top: $space-m;

			> img {
				margin-bottom: -15px;
			}

			p {
				//border-left: 2px solid $color-blue;

				&:before {
					color: $color-blue;
				}
			}
		}

		a[href="#reload"] {
			display: none;
		}

		.wds-seo-analysis {
			.wds-seo-analysis-keyword-tab-item {
				text-transform: none;
			}

			// Checks - Keyword used check
			.wds-keywords-used {

				&-table {
					table-layout: auto;

					tbody td:first-child {
						width: 100%;
					}

					thead tr th {
						padding-top: 10px;
						padding-bottom: 10px;
					}

					.sui-button {
						font-size: 12px;
						line-height: 16px;
					}
				}

				&-post {

					&-title {
						display: flex;
						align-items: center;

						.sui-tag {
							font-size: 8px;
							font-weight: bold;
							line-height: 8px;
							padding: 0 4px;
							min-height: 12px;
							text-transform: uppercase;
						}
					}

					&-link .sui-icon-link:before {
						color: inherit;
					}
				}
			}
		}

		.wds-accordion {
			display: flex;
			flex-direction: column;
			margin-bottom: 0;
			border-bottom: none;

			.wds-check-item {
				&:last-child {
					border-bottom: 1px solid $color-border-grey;
				}
			}

			.wds-check-error {
				order: 1;
			}

			.wds-check-warning {
				order: 2;
			}

			.wds-check-success {
				order: 3;
			}

			.wds-check-invalid {
				order: 4;
			}
		}

		.wds-focus-keyword {
			position: relative;

			.sui-label {
				&.wds-label {
					font-size: 13px;
				}
			}

			.sui-description {
				&.wds-description {
					margin-bottom: 10px;
				}
			}

			.wds-added-keywords-tags,
			.wds-notice {
				margin-top: 20px;
			}
		}

		.wds-more-info {
			@include margin('top');
			@include gutter('top');
			border-top: 1px solid $color-border-grey;
		}
	}

	// Readability tab
	.wds_readability {
		#wds-readability-stats {
			@include pull('left');
			@include widen;
			background-color: $color-bg-grey;

			&:not(.sui-unbranded):not(.sui-rebranded) {
				@include media(min-width, md) {
					background-image: url("#{$image-path}graphic-readability-info.svg");
					background-size: auto 131px;
				}
			}

			.sui-summary-segment {
				&:last-child {
					text-align: center;

					button {
						margin-top: $space-xs;
					}
				}
			}
		}

		.wds-interstitial-text {
			margin-top: $space;
			text-align: center;

			small {
				margin: 0 $space-xxs;
			}
		}

		.wds-accordion {
			.sui-accordion-item-header {
				.sui-tag {
					margin-right: $space-xs;
					margin-left: auto;

					+ .sui-accordion-open-indicator {
						margin-left: 0;
					}
				}
			}
		}

		.sui-table {
			margin-top: $space-m;

			tbody tr:nth-child(2n+1) {
				background: $color-bg-grey;
			}
		}

		.sui-notice {
			&:not(.wds-analysis-unsupported) {
				margin-bottom: 0;
			}
		}
	}

	@at-root {
		.wds-post-box-fields {
			[class*="wds-icon-"] {
				font-size: 16px;
				vertical-align: middle;
				margin-right: 4px;
			}

			.wds-icon-magnifying-glass-search {
				@include icon(before, magnifying-glass-search);
			}

			.wds-icon-monitor {
				@include icon(before, monitor);
			}

			.wds-status-success [class*="wds-icon-"]:before {
				color: $color-tag-green;
			}

			.wds-status-warning [class*="wds-icon-"]:before {
				color: $color-tag-yellow;
			}

			.wds-status-error [class*="wds-icon-"]:before {
				color: $color-tag-red;
			}
		}
	}
}
