@include wrap-class('.wds-page-network-settings') {

	.tab_network_settings {
		.wds-separator-top {
			@include gutter('top');
			@include margin('top');
		}

		.sui-description:not(:last-child) {
			margin-bottom: $space-m;
		}

		.sui-checkbox {
			margin-bottom: $space-xs;
			&:last-of-type {
				margin-bottom: 0;
			}

			span {
				font-size: $font-size-s;
			}
		}

		.wds-drastic-changes-notice {
			@include margin('top');
		}
	}
}
