@include wrap-class {

  .sui-toggle {

    &-wrapper {

      &-full {
        width: 100%;
      }
    }

    &-full {
      width: 100%;
    }

    &-inverted {

      > input {

        ~ .sui-toggle-slider {
          background-color: $blue;

          &:before {
            transform: translateX(#{$toggle-width - ($toggle-height - 2px) - 2px});
          }
        }

        &:checked {

          ~ .sui-toggle-slider {
            background-color: $fiftyshades;

            &:before {
              transform: translateX(0);
            }
          }
        }

        &:hover {

          ~ .sui-toggle-slider {
            box-shadow: 0 0 0 5px $blue-ghost;
          }

          &:checked ~ .sui-toggle-slider {
            box-shadow: 0 0 0 5px $smoke;
          }

          &:focus {
            box-shadow: 0 0 0 5px #D1EAF4;

            &:checked ~ .sui-toggle-slider {
              box-shadow: 0 0 0 5px $silver;
            }
          }
        }

        &:focus {

          ~ .sui-toggle-slider {
            box-shadow: 0 0 0 5px #D1EAF4;
          }

          &:checked ~ .sui-toggle-slider {
            box-shadow: 0 0 0 5px $silver;
          }
        }

        @media (forced-colors: active) and (prefers-color-scheme: dark), (-ms-high-contrast: white-on-black) {
          ~ .sui-toggle-slider {
            background-color: map-get($button-background, orange) !important;

            &:before {
              background-color: $cloud;
            }
          }

          &:checked {
            ~ .sui-toggle-slider {
              background-color: $fiftyshades !important;

              &:before {
                background-color: $white;
              }
            }
          }
        }
      }
    }

    &.sui-toggle-label-start {

      .sui-toggle-slider {
        left: auto;
        right: 0;

        ~ .sui-toggle-label {
          margin-left: 0;
          margin-right: 44px;
          width: calc(100% - 44px);
        }
      }
    }
  }

  .sui-field-list-item .sui-toggle .sui-toggle-label {
    font-size: 13px;
    line-height: 22px;
  }
}
