.smartcrawl-notice {
	padding: 0;
	margin: 5px 0 30px;
	border: 1px solid #E5E5E5;
	background: #FFF;
	overflow: hidden;
	-webkit-border-radius: 6px;
	border-radius: 6px;
	-webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.05);
	position: relative;
	z-index: 1;
	min-height: 80px;
	display: table; /* The magic ingredient! */
	font: 13px "Roboto", sans-serif;

	.smartcrawl-notice-only-admins {
		line-height: 22px;
		color: #888888;
	}
}

.smartcrawl-notice.notice.loading:before {
	content: attr(data-message);
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(255, 255, 255, 0.7);
	z-index: 5;
	text-align: center;
	line-height: 80px;
	font-size: 22px;
	font-weight: bold;
}

.smartcrawl-notice > div {
	display: table-cell; /* The magic ingredient! */
	vertical-align: middle;
	cursor: default;
	line-height: 1.5;
}

.smartcrawl-notice.notice.loading > div {
	-webkit-filter: blur(2px);
	-moz-filter: blur(2px);
	-o-filter: blur(2px);
	-ms-filter: blur(2px);
	filter: blur(2px);
}

.smartcrawl-notice-logo {
	padding-left: 30px;
}

.smartcrawl-notice-message {
	color: #23282D;
	font-size: 13px;
	font-weight: normal;
	line-height: 20px;
	padding: 20px;
	-webkit-font-smoothing: antialiased;
	width: 100%;
}

.smartcrawl-notice-cta {
	background: #F8F8F8;
	padding: 0 30px;
	position: relative;
	white-space: nowrap;

	.sui-button {
		padding: 10px 20px !important;
	}
}

.wp-core-ui .smartcrawl-notice-cta button,
.wp-core-ui .smartcrawl-notice-cta .button-primary:active {
	vertical-align: middle;
}

.wp-core-ui .smartcrawl-notice-cta input[type="email"] {
	vertical-align: middle;
	line-height: 20px;
	margin: 0;
	min-width: 50px;
	max-width: 320px;
	text-align: center;
	padding-left: 0;
	padding-right: 0;
}

.smartcrawl-notice-dismiss {
	background: transparent;
	border: 0;
	cursor: pointer;
	color: #C5C5C5;
	-webkit-font-smoothing: antialiased;
}

.smartcrawl-notice-dismiss:hover {
	color: #666;
}

.wp-smartcrawl-show-xl {
	display: none;
}

@media only all and (min-width: 1700px) {
	.wp-smartcrawl-hidden-xl {
		display: none;
	}
	.wp-smartcrawl-show-xl {
		display: block;
	}
}

@media only all and (max-width: 1200px) {
	.smartcrawl-notice-dismiss {
		display: block;
		margin: 0 auto;
		line-height: 18px;
		padding-top: 8px;
		padding-bottom: 2px;
	}
}

@media only all and (max-width: 1000px) {
	.smartcrawl-notice.notice {
		display: block;
		font-size: 13px;
	}

	.smartcrawl-notice > .smartcrawl-notice-logo {
		float: left;
		display: inline-block;
		height: 80px;
		margin: 10px;
		border-radius: 4px;
	}

	.smartcrawl-notice > .smartcrawl-notice-message {
		width: auto;
		display: block;
		min-height: 80px;
	}

	.smartcrawl-notice > .smartcrawl-notice-cta {
		display: block;
		border-top: 1px solid #E5E5E5;
		border-left: 0;
		text-align: center;
		white-space: normal;
		line-height: 30px;
		padding: 10px 20px;
	}

	.wp-core-ui .smartcrawl-notice > .smartcrawl-notice-cta > input[type="email"],
	.smartcrawl-notice > .smartcrawl-notice-cta > button {
		font-size: 14px;
	}

	.smartcrawl-notice > .smartcrawl-notice-cta > .smartcrawl-notice-dismiss {
		display: inline-block;
		float: none;
		line-height: 26px;
		padding-top: 0;
		padding-bottom: 0;
		font-size: 13px;
	}
}

@media only all and (max-width: 500px) {
	.wp-core-ui .smartcrawl-notice > .smartcrawl-notice-cta > input[type="email"],
	.smartcrawl-notice > .smartcrawl-notice-cta > button {
		display: block;
		width: 100% !important;
		max-width: none;
		margin-bottom: 4px;
		font-size: 16px;
		height: 34px;
	}

	.smartcrawl-notice > .smartcrawl-notice-cta > .smartcrawl-notice-dismiss {
		margin-top: 5px;
		font-size: 14px;
		height: 23px;
	}
}
