@include wrap-class {

	.wds-mascot-message {
		min-height: 160px;

		@include media(max-width, md) {
			min-height: unset;
		}

		.wds-mascot-bubble-dismiss {
			background: $color-bg-grey;
			float: right;
			padding: 4px 9px;
			height: $space;
			width: $space;
			cursor: pointer;
			border-radius: 4px;
			margin-left: $space-xs;
			display: inline-flex;
			align-items: center;
		}

		@include no_branding {
			display: none;
		}
	}
}
