// Report
@include wrap-class {
	.wds-report {
		.sui-accordion.sui-table {
			border-collapse: collapse;
		}

		.wds-check-item {

			p, ul, ol {
				color: $color-grey-light;
				margin-top: $space-xxs;
				margin-bottom: $space-xs;

				strong {
					color: $color-gray;
				}

				&:last-child {
					margin-bottom: 0;
				}
			}

			ul, ol {
				li {
					margin-bottom: 3px;

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			ol {
				margin-left: 0;
				list-style: decimal inside;
			}

			.sui-box-body {
				&,
				& * {
					// TODO the following is to be fixed in SUI at which point we'll have to remove this override
					// https://app.asana.com/0/164174452236961/941529846545497
					font-size: 13px;
					line-height: 22px;
				}
			}

			table {
				@extend .sui-table;

				td {
					word-break: break-all;
				}
			}

			.wds-overview,
			.wds-status,
			.wds-fix {
				margin-bottom: $space;

				&:last-child {
					margin-bottom: 0;
				}
			}

			&.sui-warning {
				.report.status.warning {
					border-left-color: $color-tag-yellow;

					> p:first-of-type:before {
						color: $color-tag-yellow;
					}
				}
			}

			&.sui-error {
				.report.status.critical {
					border-left-color: $color-tag-red;

					> p:first-of-type:before {
						color: $color-tag-red;
					}
				}
			}

			&.sui-success {
				.report.status.ok {
					border-left-color: $color-tag-green;

					> p:first-of-type:before {
						color: $color-tag-green;
					}
				}
			}

			.wds-unignore {
				margin-left: auto;
			}

			&.wds-check-item {
				.report.status {
					position: relative;
					display: block;
					border: 1px solid #E6E6E6;
					border-left: 2px solid #888;
					border-radius: 4px;
					padding: 15px 20px 15px 50px;
					background-color: #FFFFFF;
					margin: 5px 0 10px 0;

					p {
						margin-bottom: 0;
						margin-top: 0;
						color: $color-dark-alt;

						@include icon(before, info);

						&:before {
							position: absolute;
							top: 18px;
							left: 22px;
							font-size: 16px;
						}
					}
				}
			}
			.wds-recommendation {
				.wds-keywords-used-table {
					table-layout:auto;

					th {
						white-space: normal;
					}
					td:nth-child(1) {
						width: 70%;
					}
					td:nth-child(2) {
						width: 30%;
						text-align: right;
					}
					.sui-description {
						word-break: break-word;
					}
					.sui-button {
						font-size: 12px;
						line-height: 16px;

						span {
							font-size: 12px;
							line-height: 16px;
						}
					}
				}
				.wds-keywords-used-post-title {
					display: flex;
					align-items: center;

					.sui-tag {
						font-size: 8px;
						font-weight: bold;
						line-height: 8px;
						padding: 0 4px;
						min-height: 12px;
						text-transform: uppercase;
					}
				}
				.wds-keywords-used-post-link {
					text-decoration: none;
					color: #17A8E3;
					font-weight: 500;
					outline-color: rgba(0,0,0,0);
					outline-style: none;
					box-shadow: none;

					.sui-icon-link::before {
						color: inherit;
					}

				}
			}
		}
	}
}
