@include wrap-class('.wds-page-onpage') {
	#tab_homepage,
	#tab_static_homepage {
		.sui-box-footer {
			@include pull('left');
			@include pull('right');
			@include pull('bottom');
		}

		.sui-box-settings-row:last-of-type {
			border-bottom: none;
			margin-bottom: 0;
		}

	}

	#tab_static_homepage {
		.wds-description-row {
			padding-bottom: $space-gutter-md;
			border-bottom: 1px solid $color-border-grey;
		}
	}

	#tab_archives {
		.sui-accordion-item-header {
			.sui-tooltip {
				margin-right: 10px;
				margin-left: auto;

				+ .sui-accordion-open-indicator {
					margin-left: 0;
				}
			}
		}
	}

	#tab_settings {
		.wds-onpage-character-lengths {
			.sui-description {
				margin-bottom: 10px;
			}
		}
	}

	.wds-title-row,
	.wds-description-row {
		padding-bottom: 0;
		border-bottom: none;

		.wds-notice {
			margin-bottom: 0;
			margin-top: 10px;
		}

		.sui-box-settings-col-2 {
			.sui-label {
				color: #AAA;
				line-height: 22px;

				span {
					font-weight: 400;
					padding-left: 5px;
				}
			}
		}
	}

	.wds-description-row {
		.select-list-container > button:not(:hover) {
			border-bottom-color: transparent;
		}
	}

	.wds-social-row {
		.sui-checkbox {
			margin-top: $space-xs;

			input + span + span {
				font-size: $font-size-s;
			}
		}
	}

	.wds-social-row {
		margin-top: $space;
		padding-top: $space;
		border-top: 1px solid $color-border-grey;
	}

	.sui-accordion-flushed {
		border-top: none;
	}

	.sui-description {
		.sui-notice {
			margin-top: $space-s;
		}
	}

	#wds-supported-macros-modal table {
		text-align: left;
		vertical-align: top;

		tbody tr:last-of-type td {
			border-bottom: 1px solid $color-border-grey;
		}
	}
}
