@include wrap-class {
	.wds-disabled-component {
		text-align: center;
		padding-bottom: 30px;
		padding-top: 30px;
		max-width: 600px;
		margin: 0 auto;

		p {
			margin-bottom: $space;
		}

		.wds-notice {
			margin-bottom: $space;
		}
	}

	.wds-disabled-image {
		width: 128px;
		height: auto;
	}
}
