@include wrap-class {
	.wds-preset-separators {
		$box-size: 40px;

		@include cf;

		input[type="radio"] {
			@extend .sui-screen-reader-text;
		}

		.separator-selector {
			background: $color-light;
			border: 1px solid $color-grey-lighter;
			border-radius: 4px;
			float: left;
			margin-right: $space-xs;
			margin-bottom: $space-xs;
			min-width: $box-size;
			text-align: center;
			height: $box-size;
			line-height: $box-size - 2px;
		}

		input[type="radio"]:checked + label.separator-selector {
			border: 1px solid $color-blue;
			background-color: #E1F6FF;

			@include high_contrast {
				border: 1px solid $color-black;
				background-color: $color-black;
				color: #FFF;
				font-weight: bold;
			}
		}
	}
}
