@include wrap-class {
	.wds-configs-container {
		.sui-modal .sui-description strong {
			color: #333333;
		}

		.sui-accordion.sui-accordion-flushed {
			@include pull('left');
			@include widen;

			.sui-icon-smart-crawl {
				background: #000;
				width: 30px;
				border-radius: 10px;
				height: 30px;
				padding: 6px 7px;

				&:before {
					color: #CD0F2B;

					@include high_contrast {
						color: #CD0F2B !important;
					}
				}
			}

			.sui-accordion-item-header {
				.sui-accordion-col-3:nth-child(3) {
					justify-content: flex-end;
				}

				button.sui-accordion-item-action {
					margin-right: 10px;
					display: none;
				}

				@include media(min-width, md) {
					&:hover {
						button.sui-accordion-item-action {
							display: block;
						}
					}
				}

				.sui-dropdown {
					[class*=sui-icon] {
						margin-right: 0;
					}

					ul {
						min-width: 192px;
					}
				}

				.sui-accordion-item-title img {
					margin-left: 5px;
				}

				.wds-config-description,
				.wds-config-timestamp,
				.wds-config-apply-button {
					display: none;
				}

				@include media(min-width, md) {
					.wds-config-description,
					.wds-config-timestamp,
					.wds-config-apply-button {
						display: block;
					}
				}
			}

			.sui-accordion-item-body {
				table {
					border: none;

					tr:nth-child(2n) {
						background: #f8f8f8;
					}

					th, td {
						border-bottom: none;
					}

					tr:not(:first-child) {
						td {
							padding-top: 15px;
						}
					}

					tr:not(:last-child) {
						td {
							padding-bottom: 15px;
						}
					}

					td,
					td p {
						color: #888;
						font-weight: 500;
					}
				}
			}
		}

		#wds-text-config-description {
			text-align: left;
		}

		#wds-configs-list {
			position: relative;

			#wds-configs-list-loader {
				display: none;
				position: absolute;
				left: 0;
				right: 0;
				height: 100%;
				background: rgba(255, 255, 255, 0.85);
				bottom: 0;
				z-index: 5;
				vertical-align: middle;
				align-items: center;
				justify-content: space-around;
				@include pull('left');
				@include widen;

				.sui-icon-loader:before {
					vertical-align: -1px;
					margin-right: 3px;
				}
			}

			&.syncing {
				#wds-configs-list-loader {
					display: flex;
				}
			}

			#wds-configs-list-inner {
				.wds-config-details {
					display: flex;
					justify-content: space-between;
					padding-bottom: 30px;
					border-bottom: 1px solid #e7e7e7;

					p.sui-description {
						margin-bottom: 0;
					}

					.sui-button {
						min-width: auto;
						padding-right: 0;

						> span {
							width: 12px;
						}
					}
				}

				.sui-table {
					margin-top: 15px;

					.health td > span:last-of-type:after,
					.advanced td > span:first-of-type:after {
						content: "Pro";
						background-color: #8D00B1;
						color: #FFFFFF;
						min-height: 12px;
						padding: 1px 8px 1px;
						border: 0;
						border-radius: 6px;
						font-size: 8px;
						line-height: 9px;
						text-align: center;
						text-transform: uppercase;
						margin-left: 3px;

						@include is_member {
							display: none;
						}
					}
				}

				> .sui-description {
					margin-top: 30px;
					text-align: center;
				}

				.sui-row {
					margin-bottom: 3px;
					@include pull('left');
					@include widen;

					[class*="sui-col-"]:first-child {
						@include gutter('left');
					}

					[class*="sui-col-"]:not(:first-child) {
						display: none;
					}

					@include media(min-width, md) {
						[class*="sui-col-"]:not(:first-child) {
							display: block;
						}
					}
				}

				.sui-pagination-wrap {
					margin-top: 30px;

					&:first-child {
						margin-top: 0;
						margin-bottom: 30px;
					}
				}
			}

			.sui-notice-purple {
				@include margin('top');

				.sui-button-purple {
					margin-top: 10px;
				}
			}
		}

		#wds-config-modal {
			.sui-error-message {
				text-align: left;
			}
		}
	}
}
