$space: 30px;
$space-m: 20px;
$space-s: 15px;
$space-xs: 10px;
$space-xxs: 10px;
$space-gutter: $sui-gutter;
$space-gutter-md: $sui-gutter-md;

$font-alt: "Roboto", Arial, sans-serif;
$font-size: 15px;
$font-size-s: 13px;
$font-weight-bold: 500;

$radius: 5px;

$color-black: $black;
$color-dark-alt: #333;
$color-border-grey: palette(silver, soft);
$color-bg-grey: palette(silver, light);
$color-red: palette(red);
$color-blue: #17a8e3;
$color-purple: #8D00B1;
$color-modal-overlay-bg: $modal-overlay-bg-color;
$color-box-header-border: $box-header-border-color;
$color-light: #f4f4f4;
$color-grey-light: palette(gray, light);
$color-grey-lighter: #ddd;
$color-grey-subtle: #f0f0f0;
$color-gray: $gray;
$color-gray-alt: $gray-alt;
$color-select-container-border: $select-container--border-color;
$color-select-container-bg: $select-container--background;
$color-table-border: $table--border-color;

$form--input-bg-color: #fafafa;
$color-input-bg: $form--input-bg-color;

//$color-toggle-slider-bg: $toggle-slider-bg-color;
//$color-toggle-slider-bg-hover: $toggle-slider-bg-hover-color;
//$color-toggle-slider-before-checked-bg: $toggle-slider-before-checked-bg-color;
//$color-toggle-slider-active-bg-hover: $toggle-slider-active-bg-hover-color;

$color-checkbox-bg: $radio-checkbox--background;
$color-checkbox-border: $radio-checkbox--border-color;
$color-checkbox-checked-bg: $radio-checkbox--checked-background;

$color-tag-red: $tag--red-background;
$color-tag-yellow: $tag--yellow-background;
$color-tag-green: $tag--green-background;

$sui-class: '.sui-' + str-replace($sui-version, '.', '-');
$sui-wrap-class: '.sui-wrap';
