@include wrap-class('.wds-page-schema') {

	#tab_advanced {
		#wds-schema-archives-table {
			&,
			.sui-table {
				border-top: none;
			}

			> tbody {
				td {
					border-top: 1px solid $color-border-grey;
					border-bottom: none;

					&:last-child:not(:first-child) {
						display: flex;
						justify-content: flex-end;

						.sui-form-field {
							//align-self: center;
							margin-top:15px;

						}
						@include media('max-width', sm) {
							.sui-form-field {
								//align-self: center;
								margin-top:0px;
								margin-bottom:15px;
							}
						}
						@include media('max-width', md) {
							.sui-form-field {
								//align-self: center;
								margin-top:5px;
								margin-bottom:15px;
							}
						}
					}
				}

				> tr {
					&:nth-child(2n+1) > td {
					}

					&:nth-child(2n) > td {
						border-top: none;
						padding: 0 $space $space;
					}
				}
			}
		}

		#sub-settings-schema_enable_video {
			[data-panes] {
				.sui-tab-boxed:first-child {
					display: none;
				}
			}
		}
	}

	#tab_types {

		.sui-accordion-item-header {
			background: #FFF;
			position: relative;

			.sui-dropdown.sui-accordion-item-action {
				[class*="sui-icon-"]:not(.sui-icon-loader) {
					margin-right: 0;

					&,
					&:before {
						display: inline-block;
					}
				}

				li:last-child {
					[class*="sui-icon-"]:before,
					button {
						color: $color-tag-red;
					}
				}
			}
		}

		.sui-accordion-item--open {
			.sui-accordion-item-header {
				transition: none;
			}

			.sui-accordion-item-body {
				border-top: 1px solid $color-border-grey;
			}
		}

		.wds-comparison-operator {

			[type="checkbox"] {
				display: none;
			}

			span {
				padding: 3px 9px;
				background: #f8f8f8;
				color: #999999;
				border-radius: 100%;
				font-size: 20px;
				display: none;
				cursor: pointer;
				width: 28px;
				height: 28px;
			}

			.wds-equals {
				padding-left: 8px;
				font-size: 22px;
			}

			[type="checkbox"]:not(:checked) ~ .wds-not-equals {
				display: inline-block;
				color: $color-dark-alt;
			}

			[type="checkbox"]:checked ~ .wds-equals {
				display: inline-block;
			}
		}

		.wds-schema-type-rules {
			@include pull('left');
			@include widen;
			position: relative;
			color: $font-color;
			font-size: $box-selector--font-size-lg;
			line-height: $box-selector--line-height-lg;
			letter-spacing: $font--letter-spacing;
			margin-top: 0;
			margin-bottom: 0;
			padding: $space 50px;
			background: $white;
			border-bottom: 1px solid $color-border-grey;
			border-radius: $border-radius;

			@include media(max-width, md) {
				padding: $space-gutter-md $space;
			}

			&:not(:first-child) {
				margin-top: $space;
			}

			.sui-icon-link {
				font-size: 17px;
				margin-left: -21px;
				margin-right: 5px;

				&:before {
					color: #17A8E3;
				}
			}

			.sui-description {
				margin-top: 0;
			}
		}

		#wds-schema-types-footer {
			background-image: url("#{$image-path}graphic-posteditor-schema.svg");
			background-position: bottom 0 center;
			background-repeat: no-repeat;
			padding: 30px 0 140px;
			background-size: 120px;
			text-align: center;

			@include no_branding {
				background: transparent;
				padding-bottom: 40px;
			}

			.sui-button-dashed {
				background: #F8F8F8;
				margin-bottom: $space;
			}

			.sui-notice {
				text-align: left;
			}
		}

		.sui-accordion-item:first-child {
			.sui-accordion-item-header {
				border-radius: 5px;
			}
		}

		.wds-schema-type-condition-group {
			margin-bottom: $space;

			&:first-of-type {
				padding-top: $space;
			}

			> span {
				font-size: 12px;
				line-height: 20px;
				font-weight: 500;
				color: #AAA;
				display: inline-block;
				margin-bottom: 3px;
			}
		}

		.wds-schema-type {

			&-condition {
				position: relative;
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				margin-bottom: $space-s;

				&:first-child {
					margin-top: 0;
				}

				&:last-child {
					margin-bottom: 0;
				}

				&-lhs,
				&-rhs,
				&-operator,
				&-and,
				&-close {
					margin-bottom: $space-xxs;
				}

				&-lhs,
				&-rhs {
					flex: 1 0 0;
					min-width: 150px;
				}

				&-lhs {
					ul.select2-results__options {
						ul.select2-results__options {
							max-height: none;
						}
					}

					.select2-selection__rendered,
					.select2-results__option {
						span .sui-tag {
							color: #888888;
						}
					}
				}

				&-operator {
					padding: 0 $space-xs;
				}

				&-and {
					padding-left: $space-xs;

					.sui-button {
						min-width: 40px;
						width: 40px;
						padding-left: 0;
						padding-right: 0;
					}
				}

				&-close {
					display: none;
					cursor: pointer;
					position: absolute;
					transform: translateX(100%);
					right: -$space-xs;

					@include media(max-width, md) {
						right: -$space-xxs;
					}

					span {
						line-height: 1;

						&:before {
							color: $color-tag-red;
						}
					}
				}

				&:hover {
					.wds-schema-type-condition-close {
						display: block;
					}
				}
			}

			&-property-table {
				background: $white;
				table-layout: auto;

				tr {
					&:hover {
						span.sui-icon-trash {
							display: inline-block;
						}
					}
				}

				td {
					padding-top: $space-s;
					padding-bottom: $space-s;
					vertical-align: top;

					textarea,
					input {
						display: block;
						width: 100%;
						height: 30px;
						min-height: 30px;
						background: $form--input-bg-color;
						border: 1px solid #DDDDDD;
						resize: none;
					}

					&.wds-schema-property-label {
						padding-top: $space-m;
					}

					&.wds-schema-property-value {

						textarea {
							max-height: 200px;

							&::placeholder {
								font-size: 12px;
								line-height: 16px;
								color: #AAAAAA;
								padding-top: 4px;
								padding-left: 5px;
							}
						}
					}

					&.wds-schema-property-delete {
						width: 22px;
						padding: 18px 0 0 0;

						.sui-icon-trash {
							display: none;
							font-size: 12px;

							&:before {
								color: $color-tag-red;
								cursor: pointer;
							}
						}
					}

					&.wds-schema-nested-properties,
					&.wds-schema-repeating-properties {
						padding: 0;
					}

					@include media('max-width', sm) {
						padding-top: $space-xxs;
						padding-bottom: $space-xxs;

						&.wds-schema-property-label {
							padding-top: $space-s;

							> .sui-icon-trash {
								float: right;
								font-size: 12px;

								&:before {
									color: $color-tag-red;
									cursor: pointer;
								}
							}
						}

						&.wds-schema-property-value {
							padding-bottom: $space-s;
						}

						&.wds-schema-property-delete {
							display: none;
						}
					}
				}

				tr.wds-schema-property-source-duration {

					td.wds-schema-property-value {

						span {
							position: relative;
							display: inline-block;
							width: 33.333%;
							padding: 0 1px;

							&:after {
								position: absolute;
								right: 9px;
								top: 50%;
								transform: translateY(-50%);
								font-weight: $font-weight-bold;
								color: $form--label-color;
							}

							&:nth-child(2):after {
								content: 'h';
							}

							&:nth-child(3):after {
								content: 'm';
							}

							&:nth-child(4):after {
								content: 's';
							}

							[type="number"] {
								padding-right: $space-m;
							}

							@include media('max-width', md) {
								display: block;
								width: 100%;
								padding: 1px 0;
							}
						}
					}
				}

				tfoot tr td {
					height: auto;
					padding: $space;

					div.schema-type-footer-controls {
						width: 100%;
						text-align: right;
					}
				}
			}
		}


		#wds-add-property-modal,
		#wds-add-schema-type-modal,
		#wds-change-property-type-modal {
			.sui-box-selectors,
			.wds-box-selector-message {
				@include pull('left');
				@include widen;
				border-top: 1px solid $color-border-grey;
				border-bottom: 1px solid $color-border-grey;
			}

			.sui-box-selectors {
				ul li {
					padding: 5px;
				}
			}

			&-buttons {
				display: flex;
				justify-content: space-between;
			}

			.wds-box-selector-message {
				background: url("#{$image-path}graphic-no-properties.svg") no-repeat center 40px #F8F8F8;
				background-size: 70px 70px;
				padding-top: 110px;
				padding-bottom: 40px;

				@include no_branding {
					display: none;
				}

				@include margin('bottom');

				h3 {
					color: #666666;
					margin-bottom: 0;
				}
			}
		}

		#wds-add-property-modal {
			p:last-of-type:not(:last-child) {
				margin-bottom: 30px;
			}
		}

		.wds-missing-properties-notice {
			text-align: left;

			p span {
				color: $color-blue;
			}
		}

		#wds-add-schema-type-modal {
			max-width: 820px;

			.sui-box-selectors {
				border-top: none;
				margin-top: 0;
				max-height: 400px;
				overflow-y: auto;

				&:nth-child(2) {
					border-top: 1px solid $color-border-grey;
				}
			}

			.wds-box-selector-message {
				padding-top: 80px;
				background-position: center 10px;
				border-top: none;
				background-color: #ffffff;
				background-image: url("#{$image-path}graphic-schema-disabled.svg");
			}

			h3 .sui-tag-sm {
				margin-bottom: 10px;
			}

			#wds-add-schema-type-breadcrumbs {
				@include pull('left');
				@include gutter('left');
				@include gutter('right');
				@include widen;

				padding-top: 15px;
				padding-bottom: 15px;
				border-top: 1px solid $color-border-grey;
				border-bottom: 1px solid $color-border-grey;
				text-align: left;
				color: #AAA;

				> span {
					font-size: 12px;
					line-height: 20px;
					font-weight: 500;
					margin-right: 5px;
					color: #AAA;

					&:last-child {
						span.sui-icon-chevron-right {
							display: none;
						}
					}
				}

				span.sui-icon-chevron-right {
					margin-left: 5px;
				}
			}

			#wds-add-schema-type-label,
			#wds-add-schema-type-conditions {
				@include pull('left');
				@include gutter('left');
				@include gutter('right');
				@include gutter('bottom');
				@include margin('bottom');
				@include widen;

				border-bottom: 1px solid $color-border-grey;
			}

			#wds-add-schema-type-label {
				background: url("#{$image-path}graphic-readability-info.svg") center bottom no-repeat;
				background-size: auto 131px;
				padding-bottom: 160px;

				@include no_branding {
					background-image: none;
				}
			}

			#wds-add-schema-type-conditions {
				text-align: left;

				.wds-schema-type-condition-group {
					&, &:first-child {
						padding-top: 0;
						padding-bottom: 0;
						border-bottom: none;
					}
				}
			}

			#wds-search-sub-types {
				@include pull('left');
				@include widen;

				@include gutter('left');
				@include gutter('right');
				@include gutter('top');

				background: #F8F8F8;
			}
		}

		.wds-schema-repeating-properties,
		.wds-schema-nested-properties {
			padding: 0;

			.sui-accordion {
				border: none;

				> .sui-accordion-item {
					> .sui-accordion-item-header {
						[class*="sui-icon-"] {
							margin-right: 0;
						}

						.sui-accordion-item-action {
							display: none;
						}
					}

					&:hover {
						> .sui-accordion-item-header .sui-accordion-item-action {
							display: inline-block;
						}
					}

					&.sui-accordion-item--open {
						> .sui-accordion-item-header {
							background: #F8F8F8;
						}

						> .sui-accordion-item-body {
							border-top: none;
							border-bottom: 1px solid transparent;
						}
					}
				}
			}

			.sui-tag.sui-tag-sm {
				margin-left: 5px;
			}
		}

		.wds-schema-nested-properties,
		.wds-schema-repeating-properties {
			> .sui-accordion > .sui-accordion-item > .sui-accordion-item-body {
				> .sui-table {
					> tfoot tr td {
						padding-right: $space;
						padding-bottom: $space-s;
						padding-top: $space-s;

						button {
							@include media('min-width', md) {
								float: right;
							}

							&:first-child:not(:last-child) {
								margin-right: 0;
							}

							&:last-child:not(:first-child) {
								margin-right: 10px;
							}
						}
					}
				}
			}
		}

		.wds-schema-repeating-properties {
			> .sui-accordion > .sui-accordion-item > .sui-accordion-item-body {
				> .sui-table {
					> thead tr td.wds-schema-repeatable-title {
						div.wds-schema-repeatable-title-inner {
							display: flex;
							justify-content: space-between;
							align-items: center;
						}

						.wds-schema-repeatable-button-controls {
							display: none;

							button {
								margin: 0;
							}
						}
					}

					&:hover {
						> thead tr td.wds-schema-repeatable-title {
							.wds-schema-repeatable-button-controls {
								display: block;
							}
						}
					}
				}
			}

			.sui-table:not(:first-child) {
				margin-top: 15px;
			}

			.sui-table:not(:last-child) {
				margin-bottom: 15px;
			}
		}

		> .sui-box-body {
			position: relative;
			padding-bottom: 90px;

			#wds-save-schema-types {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
			}
		}

		.sui-date .sui-form-control {
			&,
			+ .sui-icon-calendar {
				height: 20px;
			}
		}

		.sui-accordion-item--disabled .sui-accordion-item-header {
			.sui-dropdown {
				pointer-events: all;

				.sui-icon-widget-settings-config:before {
					color: inherit;
				}

				ul {
					li:nth-child(1),
					li:nth-child(2) {
						display: none;
					}
				}
			}
		}

		.sui-row {
			margin-left: 0;
			margin-right: 0;
			margin-bottom: 3px;

			div:first-child {
				@include gutter('left');
			}

			strong {
				font-weight: 600;
			}
		}

		.sui-icon-defer:before {
			transform: rotate(90deg);
		}

		.wds-invalid-type-icon {
			&:before {
				color: $color-tag-yellow;
			}

			vertical-align: top;
			margin-left: 10px;
		}

		.wds-type-sub-text {
			display: block;
			margin-top: 20px;

			a {
				color: #666666;
				text-decoration: underline;
			}
		}

		.wds-delete-accordion-item-action {
			position: absolute;
			right: 8px;

			.sui-icon-trash:before {
				color: #FF6D6D;
			}
		}

		.sui-table-item-title > span:first-of-type {
			cursor: default;
		}

		#wds-add-schema-type-modal,
		#wds-custom-property-addition-modal {
			.sui-description strong {
				color: #333333;
			}
		}

		#wds-custom-property-addition-modal {
			@include schema_icon(property-type-simple);
			@include schema_icon(property-type-nested);
			@include schema_icon(property-type-collection);

			.sui-tab-item.active {
				[class*="wds-custom-icon-"] {
					background: #17A8E3;
				}
			}

			[class*="wds-custom-icon-"] {
				-webkit-mask-size: 24px 24px;
				vertical-align: -3px;
				margin-right: 5px;
			}

			#wds-property-type + .sui-description {
				margin-bottom: 0;
			}
		}

		#wds-schema-type-components {
			#wds-schema-types-body {
				@include pull('left');
				@include gutter;
				@include widen;

				border-top: 1px solid $color-border-grey;
				border-bottom: 1px solid $color-border-grey;
				background: #fafafa;

				> .sui-accordion {
					border: none;

					> .sui-accordion-item {
						margin-bottom: 10px;
						border-radius: 5px;
						border-bottom: none;
						box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 5%);

						&:last-of-type {
							margin-bottom: 0;
						}

						> .sui-accordion-item-header {
							border-radius: 5px;
						}

						&.sui-accordion-item--open {
							> .sui-accordion-item-header {
								border-bottom-right-radius: 0;
								border-bottom-left-radius: 0;
							}

							> .sui-accordion-item-body {
								border-bottom-right-radius: 5px;
								border-bottom-left-radius: 5px;
							}
						}

						> .sui-accordion-item-body {
							border: 1px solid #E6E6E6;
						}
					}
				}
			}
		}

		.sui-accordion-item--disabled [class*="wds-custom-icon-"] {
			background: #DDD;
		}

		.sui-table {
			.sui-accordion-item-action:not(.wds-delete-accordion-item-action) {
				@include high_contrast {
					margin: 0 10px;
				}
			}
		}
	}

	#wds-schema-types-invalid-notice p span {
		color: $color-blue;
	}
}
