@include wrap-class {
	.wds-preview {
		background: #fff;
		border: 1px solid #DFDFDF;
		padding: $space;
		border-radius: $radius;

		&-loading {
			opacity: .7;
		}

		&-container {
			padding-bottom: $space-m;
		}

		&-description {
			@include small-text;
		}

		* {
			font-family: Arial, sans-serif;
			font-weight: 400;
			letter-spacing: initial;
			text-align: left;
		}

		&-title {
			h3 {
				font-size: 18px;
				line-height: 21px;
				margin: 0;
				text-transform: none;

				a {
					color: #1a0dab;
					text-decoration: none;
				}
			}
		}

		&-url {
			font-size: 14px;
			line-height: 16px;
			margin-bottom: 2px;
			word-break: break-all;

			a {
				color: #006621;
				text-decoration: none;
			}
		}

		&-meta {
			color: #545454;
			font-size: $font-size-s;
			line-height: 18px;
		}
	}
}
