@include wrap-class {

	small {
		@include small-text;
	}

	strong,
	.sui-toggle-label {
		color: $color-gray-alt;
	}

	.sui-description {
		strong {
			color: #888888;
		}
	}

	.cf {
		&:after {
			content: "";
			display: table;
			clear: both;
		}
	}

	.wds-right {
		float: right;
	}

	.wds-space-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.wds-separator {
		&-top {
			margin-top: $space-m;
			padding-top: $space-m;
			margin-bottom: 0;
			border-top: 1px solid $color-border-grey;
		}

		&-bottom {
			margin-bottom: $space-m;
			padding-bottom: $space-m;
			border-bottom: 1px solid $color-border-grey;
		}
	}

	// @TODO: get rid of the wds-draw classes and use the mixins instead
	.wds-draw {
		&-left,
		&-left-padded {
			margin-left: -$space-gutter;
			width: calc(100% + #{$space-gutter * 2});

			@include media(max-width, md) {
				margin-left: -$space-gutter-md;
				width: calc(100% + #{$space-gutter-md * 2});
			}
		}

		&-left-padded {
			padding-left: $space-gutter;
			padding-right: $space-gutter;

			@include media(max-width, md) {
				padding-left: $space-gutter-md;
				padding-right: $space-gutter-md;
			}
		}

		&-down {
			margin-bottom: -$space-gutter;

			@include media(max-width, md) {
				margin-bottom: -$space-gutter-md;
			}
		}
	}

	.wds-no-border {
		border: none;
	}

	.wds-discreet-p {
		p {
			margin-bottom: 0;
			margin-top: 0;
		}
	}

	.wds-inverted-checkbox.sui-checkbox input[type="checkbox"] {
		& + span {
			background: $color-checkbox-checked-bg;
			border-color: $color-checkbox-checked-bg;
			@include high_contrast {
				background: #000;
				border-color: #000;
			}

			&:before {
				opacity: 1;
			}
		}

		&:checked + span {
			border-color: $radio-checkbox--border-color;
			@include high_contrast {
				border-color: #000;
			}

			background: $radio-checkbox--background;

			&:before {
				opacity: 0;
			}
		}
	}

	.hidden {
		display: none;
	}

	.sui-actions-right {
		small {
			margin-right: 10px;
		}
	}

	.wds-report {
		.wds-accordion {
			border-radius: 0;
			border-width: 1px 0;

			.sui-accordion-item-header,
			.sui-accordion-item-body {
				box-shadow: inset 3px 0 #888888;
			}

			.wds-check-item:last-child {
				.sui-accordion-item-header,
				.sui-accordion-item-body {
					border-radius: 0;
				}
			}
		}
	}

	.wds-upsell-items {
		padding-left: 0;
		padding-right: 0;

		.sui-box-settings-row {
			@include gutter('left');
			@include gutter('right');
		}

		.sui-disabled {
			@include gutter('top');
			@include gutter('bottom');
		}
	}

	.wds-upsell-tab-description {
		@include gutter('left');
		@include gutter('right');
		@include gutter('bottom');
	}

	.wds-centre {
		text-align: center;
	}

	.wds-vertical-tabs-container {
		.sui-box-footer {
			justify-content: flex-end;
		}
	}

	.sui-notice {
		.wds-notice-dismiss {
			display: block;
		}
	}

	.sui-modal-sm,
	.sui-modal-md {
		.sui-notice,
		.sui-form-field {
			text-align: left;
		}
	}

	.wds-floating-notice-trigger {
		display: none;
	}

	.select2-selection__clear {
		font-size: 18px;
		color: #888;
		margin-right: $space-xs;
	}

	.wds-documentation-link {
		@include no_branding {
			display: none;
		}
	}

	.wds-required-asterisk {
		font-size: 18px;
		color: $color-blue;
		vertical-align: middle;
		margin-left: 3px;
		display: inline-block;
		cursor: pointer;
	}

	@include high_contrast {
		.sui-dropdown ul li button {
			color: #000 !important;
		}
	}

	.sui-pagination {
		li.sui-active a {
			background-color: #E1F6FF;
			color: #17A8E3;
		}
	}
}
