@import "lighthouse";

@include wrap-class('.wds-seo-health-settings') {
	&.wds-lighthouse-settings #tab_reporting,
	#tab_settings {
		.sui-box-settings-row {
			margin-bottom: 0;
			border-bottom: none;
		}

		.sui-box-settings-row + .sui-box-footer {
			padding-bottom: 0;
		}
	}

	&.wds-lighthouse-settings #tab_reporting,
	#tab_settings {
		.sui-box-settings-row + .sui-box-footer {
			@include pull('left');
			@include widen;
		}
	}

	#tab_settings {
		.sui-tab-boxed:nth-child(2) {
			padding: 0;
			border: none;

			span.sui-description {
				margin-top: 15px;
			}
		}

		#wds-lighthouse-dashboard-widget-device {
			[data-panes] {
				display: none;
			}
		}
	}
}
