@include wrap-class {
	line-height: 1;

	.sui-col-auto {
		flex: 0 0 auto;
		width: auto;
	}

	@include media(max-width, md) {

		[class*=sui-col-md-] {
			margin-bottom: 0;
		}
	}

	[class*="sui-icon-"] {
		vertical-align: middle;
	}

	.sui-modal-content {
		.sui-box .sui-box-footer {
			justify-content: space-between;
		}
	}

	.sui-tag.sui-tag-inactive {
		color: $color-dark-alt;

		/* TODO: Revisit when the latest version of SUI is used */
		@include high_contrast {
			color: #fff;
		}
	}

	.sui-box-body {
		img.sui-image.sui-upsell-image {
			margin-bottom: -$space;
		}
	}

	.sui-box .sui-box-header {
		// Dashboard sitemap widget
		> span.sui-icon-loader {
			font-size: 18px;
			margin-left: $space-xs;
			margin-top: 1px;
		}
	}

	.sui-accordion {

		&-item {
			&:first-child,
			&:last-child {
				.sui-accordion-item-header {
					border-radius: 0;
				}
			}

			&-header {
				position: relative;
				background: $white;
				flex-wrap: wrap;

				.sui-accordion-col-auto {

					&:last-child {
						margin-left: auto;
					}
				}

				@include media(max-width, md) {
					padding-top: $space-xs;
					padding-bottom: $space-xs;

					@for $i from 1 through 12 {
						.sui-accordion-col-#{$i} {
							flex: 1 0 0;
						}
					}

					.sui-accordion-col,
					.sui-accordion-col-auto {

						&:not(:first-child) {
							padding-right: 19px;
						}
					}
				}
			}

			&-body {

				> .sui-table {
					background: $white;
				}
			}

			&-action.sui-dropdown {

				[class*="sui-icon-"]:not(.sui-icon-loader) {
					margin-right: 0;
				}
			}
		}
	}

	.sui-toggle-content {
		margin-top: 10px;
	}

	.sui-recipient {
		span {
			line-height: 29px;
		}
	}

	.sui-insert-variables {

		&-label {
			margin-right: 40px;
		}

		textarea ~ select.sui-variables + .sui-select {
			.selection span[role=combobox] {
				border: 0;
			}
		}
	}

	.sui-tag-pro {
		z-index: 3;
		cursor: pointer;
	}

	.wds-notice-wrap {
		@extend .sui-notice-dismiss;
	}

	.sui-border-frame > small strong {
		margin-bottom: 10px;
		display: inline-block;
	}

	.sui-form-field {
		.sui-error-message {
			display: none;
		}

		&.sui-form-field-error {
			.sui-error-message {
				display: block;
			}
		}

		&.sui-form-control-group {
			display: flex;
			align-items: center;
			@extend .sui-form-control;
			padding: 0 $space-xxs 0 0;

			.sui-form-control {
				flex: 1 0 0;

				&,
				&:hover,
				&:focus {
					border: 0;
					background: transparent;
					box-shadow: none;
				}
			}
		}
	}

	.sui-box-body .sui-upsell-row .sui-upsell-notice p a.sui-button-purple {
		color: #fff;
		display: table;
		margin-top: 10px;
	}

	.sui-box.sui-summary:last-child {
		margin-bottom: $space;
	}

	.sui-notice .sui-notice-content .sui-notice-message p {
		margin-top: 0;
	}

	.sui-table {
		.sui-tooltip {
			margin-top: 15px;
		}

		th, td {
			word-break: break-all;
		}

		@include media('max-width', md) {
			.sui-tooltip {
				margin-top: 5px;
			}
		}
		@include media('max-width', sm) {

			thead, tbody, tr, th, td {
				display: block;
			}

			.sui-tooltip {
				margin-top: -20px !important;
				margin-bottom: 20px !important;
			}
			tbody {

				tr {
					border-bottom: 1px solid $table--border-color;
				}

				td {
					padding-left: $space-s;
					padding-right: $space-s;
					border: 0;
					height: auto;
				}
			}
		}
	}

	.sui-label {

		.sui-label-light {
			font-weight: 300;
			white-space: pre;
		}
	}

	@include high_contrast {
		.sui-button-ghost.sui-button-icon {
			padding-left: 4px;
			padding-right: 4px;
		}
	}
}

.sui-select.sui-select-dropdown-container--open .sui-select-dropdown .select2-results .select2-results__options .select2-results__option.select2-results__message {
	padding-left: 20px !important;

	&:before {
		display: none !important;
	}
}

span.select2-container.sui-select.sui-select-dropdown-container--open {
	z-index: 99999;
}

#{$sui-class} .sui-select.sui-select-dropdown-container--open.sui-select-theme--vars .sui-select-dropdown .select2-results .select2-results__options .select2-results__option[role=option] {
	padding-top: $space-xxs;
	padding-bottom: $space-xxs;

	.sui-variable-name {
		min-width: auto;
		line-height: 1.6;
	}
}